import { BigNumber } from 'bignumber.js';

/**
 *    .      .
 *   , .. 5.1 => 6.
 *
 * @param number {BigNumber|number|string}
 * @param possiblyIncrementedDigitIndex        .    0.  ,      ,    0.
 */
export default (number, possiblyIncrementedDigitIndex) => BigNumber(number)
  .shiftedBy(-possiblyIncrementedDigitIndex)
  .dp(0, BigNumber.ROUND_UP)
  .shiftedBy(possiblyIncrementedDigitIndex);



